import { ref } from 'vue';
import axios from 'axios';
import { useCookiesLocalData } from '../local/useLocalData'


function axiosFunc() {
    const apiURL = 'https://vc-liff.realtime.tw/api';
    // const apiURL = 'http://127.0.0.1:8080/api';
    const axiosClient = axios.create({
        baseURL: apiURL,
        headers: {
            "Content-Type": "application/json; charset=utf-8",
            Accept: "application/json",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers": "*",
        },
        timeout: 10000
    });

    const apiGET = async (path) => {
        try {
            const res = await axiosClient.get(path);
            return res.data;
        } catch (error) {
            console.log('JN - API GET error:', error)
            return Promise.reject(error.message);
        }
    }

    const apiGETWithParams = async (path, params) => {
        try {
            const res = await axiosClient.get(path, {
                params,
            });
            return res.data;
        } catch (error) {
            console.log('JN - API GET error:', error)
            return Promise.reject(error.message);
        }
    };

    const apiPOST = async (path, jsonData) => {
        try {
            const res = await axiosClient.post(path, jsonData)
            return res.data
        } catch(error) {
            console.log('JN - API POST error:', error)
            return Promise.reject(error.message)
        }
    }

    return {
        apiGET,
        apiGETWithParams,
        apiPOST
    }
}

