function logAxiosFunc(jwtToken) {
    const logAPIURL = 'https://imag-analytics-api.uat.linebot-tech.com/api';
    const jwtTokenValue = jwtToken;
    const axiosClient = axios.create({
        baseURL: logAPIURL,
        headers: {
            "Content-Type": "application/json; charset=utf-8",
            Accept: "application/json",
            Authorization: `Bearer ${jwtTokenValue}`,
            'Access-Control-Allow-Origin': '*',
        },
        timeout: 10000
    });

    const apiGET = async (path) => {
        try {
            const res = await axiosClient.get(path);
            return res.data;
        } catch (error) {
            console.log('JN - API GET error:', error)
            return Promise.reject(error.message);
        }
    }

    const apiGETWithParams = async (path, params) => {
        try {
            const res = await axiosClient.get(path, {
                params,
            });
            return res.data;
        } catch (error) {
            console.log('JN - API GET error:', error)
            return Promise.reject(error.message);
        }
    };

    const apiPOST = async (path, jsonData) => {
        try {
            const res = await axiosClient.post(path, jsonData)
            return res.data
        } catch(error) {
            console.log('JN - API POST error:', error)
            return Promise.reject(error.message)
        }
    }

    return {
        apiGET,
        apiGETWithParams,
        apiPOST
    }
}

export function useLineMessageAPI() {
    const {apiGET, apiGETWithParams, apiPOST} = axiosFunc();
    var path = "/lineMessage";

    const sendMessageAPI = async(userLineId) => {
        const jsonData = {
            userId: userLineId,
            messageText: '恭喜你完成梵克雅寶冬日秘境探險！趕快領取冬日秘境的限定桌布！'
        };
        const apiPath = path + '/send_message/';
        const res = await apiPOST(apiPath, jsonData);
        console.log('JN - Line api response:', res)
        return res;
    };

    return {
        sendMessageAPI
    }
}

export function useUserRecordAPI() {
    const {apiGET, apiGETWithParams, apiPOST} = axiosFunc();
    var path = "/userRecord";

    const getUserRecordAPI = async(userLineId) => {
        const jsonData = {
            userId: userLineId,
        };
        try {
            const apiPath = path + '/get_user_record/';
            const res = await apiPOST(apiPath, jsonData);
            console.log('JN - get_user_record api response:', res);
            if (res.returnStatus == 0) {
                return res.returnData.userQuizRecord;
            } else {
                console.log('JN - error:', res);
                return 'none';
            }
        } catch(error) {
            console.log('JN - error:', error);
            return 'none';
        }
    };

    const userRecordAPI = async(userLineId, newUserQuizRecord) => {
        const jsonData = {
            userId: userLineId,
            userQuizRecord: newUserQuizRecord
        };
        const apiPath = path + '/user_record/';
        const res = await apiPOST(apiPath, jsonData);
        console.log('JN - user_record api response:', res)
        return res;
    };

    return {
        getUserRecordAPI, userRecordAPI
    }
}

export function useImageSynthesisAPI() {
    const {apiGET, apiGETWithParams, apiPOST} = axiosFunc();
    var path = "/imageSynthesis";

    const cardCreateAPI = async(imageIndex, shareName, blessMessageIndex) => {
        const jsonData = {
            imageIndex: imageIndex,
            shareName: shareName,
            blessMessageIndex: blessMessageIndex
        };
        try {
            const apiPath = path + '/card_create/';
            const res = await apiPOST(apiPath, jsonData);
            console.log('JN - get_user_record api response:', res);
            if (res.returnStatus == 0) {
                const imageURL = 'https://vc-liff.realtime.tw/cardImage//file/file?key=' + res.returnData.imageUrl;
                return imageURL;
            } else {
                console.log('JN - error:', res);
                return '';
            }
        } catch(error) {
            console.log('JN - error:', error);
            return '';
        }
    };

    return {
        cardCreateAPI,
    }
}

export function useLogLoginGetToken() {
    const {apiGET, apiGETWithParams, apiPOST} = logloginAxiosFunc();
    var path = "/liff"

    const liffLoginAPI = async(user_id, user_nickname, accessToken, liff_Id) => {
        const jsonData = {
            line_uid: user_id,
            line_name: user_nickname,
            liff_id: liff_Id,
            access_token: accessToken
        };
        try {
            const apiPath = path + '/login';
            const res = await apiPOST(apiPath, jsonData);
            console.log('JN - get_user_record api response:', res);
            const data_api_token = res.jwt;
            return data_api_token;
        } catch(error) {
            console.log('JN - error:', error);
            return '';
        }
    };

    return {
        liffLoginAPI
    }
}

export function useLogAPI(jwtToken) {
    const {apiGET, apiGETWithParams, apiPOST} = logAxiosFunc(jwtToken);
    var path = "/log"

    const checkToken = async() => {
        const jsonData = {
            path: 'liff',
            ts: Math.floor(Date.now() / 1000)
        };
        try {
            const apiPath = path + '/page';
            const res = await apiPOST(apiPath, jsonData);
            console.log('JN - check token api response:', res);
            if (res.code == 0) {
                return true;
            } else {
                return false;
            }
            
        } catch(error) {
            console.log('JN - error:', error);
            return false;
        }
    };

    const saveLog = async(categoryValue, pathValue, actionValue, eventValue, valueValue, detailValue) => {
        const jsonData = {
            category: categoryValue,
            path: pathValue,
            action: actionValue,
            event: eventValue,
            ts: Math.floor(Date.now() / 1000),
            // value: valueValue,
            // detail: {
            //     detail: detailValue
            // }
        };
        try {
            const apiPath = path + '/event';
            const res = await apiPOST(apiPath, jsonData);
            console.log('JN - save log api response:', res);
            if (res.code == 0) {
                console.log('log save success');
                return true;
            } else {
                console.log('JN - log save error:', res.message);
                return false;
            }
            
        } catch(error) {
            console.log('JN - error:', error);
            return false;
        }
    };
    return {
        checkToken, saveLog
    }
}

export function useCallLog() {
    const { saveTokenToCookies, getTokenFromCookies } = useCookiesLocalData();

    const saveLogAPI = async(categoryValue, pathValue, actionValue, eventValue, valueValue, detailValue) => {
        var jwtToken = await getTokenFromCookies();
        const { saveLog } = useLogAPI(jwtToken);
        const isSave = await saveLog(categoryValue, pathValue, actionValue, eventValue, valueValue, detailValue);
        console.log('JN - the log', detailValue, 'is save: ', isSave ? "Yes" : "No");
    };

    return {
        saveLogAPI
    }
}