                    size: 'full',
                    aspectRatio: '52:10',
                    aspectMode: 'cover',
                    action: {
                        type: 'uri',
                        uri: 'https://line.me/R/ti/p/@072chkdr',
                    }
                },
                // body: {
                //     type: 'box',
                //     layout: 'vertical',
                //     contents: [
                //         {
                //             type: 'text',
                //             text: '開 始 探 索',
