                //     offsetStart: '0px',
                //     offsetEnd: '0px'
                // }
            },
        }
    ]).then(function (res) {
        if (res) {
            // console.log(`${res.status} message send`)
            console.log(`Line share message:${res.status}, and resultDescription: `, res);
            //TODO: log api 頁面13.1
            saveLogAPI('CTA', '/sharing', 'success', 'click', '', '分享好友後，分享成功');
            if (profile.value == null) {
                userRecordAPI('JN', QuizCompleteStatus.none);
                sendMessageAPI('Ua6f72d76770efb472051582d50ab6695');
            } else {
                sendMessageAPI(profile.value.userId);
                userRecordAPI(profile.value.userId, QuizCompleteStatus.none);
            }
            //TODO: log api 頁面13.3
            saveLogAPI('download', '/sharing', 'click', 'click', '', '送桌布');
        } else {
            console.log('Share target picker is closed!')
        }
    }).catch(function (error) {
        console.log(`share target picker send error: ${error.message}`)
