const initializeLiff = async() => {
    try {
        //1551453274-ANYeG98Q JN_test 1
        //2006565980-GRlQWa6l JN_test 2
        liff.init({liffId: '2006565980-GRlQWa6l'}).then(async() => {
            if(liff.isLoggedIn()) {
                profile.value = await liff.getProfile();
                console.log('JN - get user profile:', profile.value);
                userAccessToken.value = await liff.getAccessToken();
