onBeforeMount( async() => {
    console.log('JN - before load check now quiz');
    await initializeLiff();
    if (profile.value == null) {
        const nowStatus = await getUserRecordAPI('JN');
        console.log('JN - load now page:', nowStatus);
        switch (nowStatus) {
            case QuizCompleteStatus.quiz_1:
                videoPreviewPath.value = ImagePath.quizVideoPreview_1;
                videoController.value.videoPlayerSourceChange(VideoPath.quiz_1);
                prePageStatus = PageStatus.Quiz_1Page;
                maskTitleIndex.value = 1;
                break;
            case QuizCompleteStatus.quiz_2:
                videoPreviewPath.value = ImagePath.quizVideoPreview_2;
                videoController.value.videoPlayerSourceChange(VideoPath.quiz_2);
                prePageStatus = PageStatus.Quiz_2Page;
                maskTitleIndex.value = 2;
                break;
            case QuizCompleteStatus.quiz_3:
                videoPreviewPath.value = ImagePath.quizVideoPreview_3;
                videoController.value.videoPlayerSourceChange(VideoPath.quiz_3);
                prePageStatus = PageStatus.Quiz_3Page;
                maskTitleIndex.value = 3;
                break;
            case QuizCompleteStatus.quiz_4:
                videoPreviewPath.value = ImagePath.quizVideoPreview_4;
                videoController.value.videoPlayerSourceChange(VideoPath.quiz_4);
                prePageStatus = PageStatus.Quiz_4Page;
                maskTitleIndex.value = 4;
                break;
            case QuizCompleteStatus.complete:
                isVideoShow.value = false;
                isMutedBtnShow.value = false;
                isFirstVideoPreview.value = false;
                prePageStatus = PageStatus.EndPage;
                pageStatus.value = PageStatus.EndPage;
                break;
            default:
                break;
        }
    } else {
        const nowStatus = await getUserRecordAPI(profile.value.userId);
        console.log('JN - load now page:', nowStatus);
        switch (nowStatus) {
            case QuizCompleteStatus.quiz_1:
                videoPreviewPath.value = ImagePath.quizVideoPreview_1;
                videoController.value.videoPlayerSourceChange(VideoPath.quiz_1);
                prePageStatus = PageStatus.Quiz_1Page;
                maskTitleIndex.value = 1;
                break;
            case QuizCompleteStatus.quiz_2:
                videoPreviewPath.value = ImagePath.quizVideoPreview_2;
                videoController.value.videoPlayerSourceChange(VideoPath.quiz_2);
                prePageStatus = PageStatus.Quiz_2Page;
                maskTitleIndex.value = 2;
                break;
            case QuizCompleteStatus.quiz_3:
                videoPreviewPath.value = ImagePath.quizVideoPreview_3;
                videoController.value.videoPlayerSourceChange(VideoPath.quiz_3);
                prePageStatus = PageStatus.Quiz_3Page;
