                userAccessToken.value = await liff.getAccessToken();
                console.log("JN - userAccessToken: ", userAccessToken.value);
                if (userAccessToken.value == null) {
                    userAccessToken.value = '';
                }
                var getToken = await getTokenFromCookies();
                if(getToken == "") {
                    getToken = await liffLoginAPI(profile.value.userId, profile.value.displayName, userAccessToken.value, "1551453274-ANYeG98Q");
                    console.log('JN - get token:', getToken);
                    await saveTokenToCookies(getToken);
                    userJWTToken.value = getToken;
                } else {
                    const { checkToken } = useLogAPI(getToken);
                    const isTokenOK = await checkToken();
                    if (isTokenOK) {
                        userJWTToken.value = getToken;
                    } else {
                        getToken = await liffLoginAPI(profile.value.userId, profile.value.displayName, userAccessToken.value, "1551453274-ANYeG98Q");
