import Cookies from 'js-cookie';

export function useCookiesLocalData() {

    const saveTokenToCookies = async(token) => {
        Cookies.set('token', token, {expires: 60, secure: true, sameSite: 'Strict'});
    }

    const getTokenFromCookies = async() => {
        return Cookies.get('token') ? Cookies.get('token') : "";
    }

    return {
        saveTokenToCookies, getTokenFromCookies
    }
}